//  get 
export const _get = function(url, data, success, check_login, msg){
	console.log('get')
	let _this = this
	// 
	uni.showLoading({
	  title: '...',
		mask:true
	});

	// if(!wx.getStorageSync('token')){
	// 	uni.showModal({
	// 		title: '',
	// 		content: "",
	// 		success: function (res) {
	// 			if (res.confirm) {
	// 					console.log('  ');
	// 					uni.navigateTo({
	// 						url:"/pages/login/login"
	// 					})
						
						
	// 			} else if (res.cancel) {
	// 					console.log('');
						
	// 			}
	// 		}
	// 	})
	// 	return false
	// }


	// 
	data = data || {}
	// data.wxapp_id = 10001;
	// data.applets = 25
	// data.token = uni.getStorageSync('token') || '';
	
	//get
	uni.request({
	    url: _this.api_root + url, //
	    header: {
	      'content-type': 'application/json', //
				'X-Xsh-Token': wx.getStorageSync('token') || ''
	    },
	    data: data,
	    success: (res) => {
				//success
				//
				if (res.statusCode !== 200 || typeof res.data !== 'object') {
					if (!msg) {
						uni.showModal({
						    title: '',
						    content: '',
						    success: function (res) {
									if (res.confirm) {
											console.log('');
									} else if (res.cancel) {
											console.log('');
									}
						    }
						})
					}
					return false;
				}
					// 
				if(res.data.errno === 501){
					uni.showModal({
						title: '',
						content: res.data.errmsg,
						success: function (res) {
							if (res.confirm) {
								// 
								uni.navigateTo({
									url:"/pages/login/login.vue"
								})
								
								
							} else if (res.cancel) {
									console.log('');
							}
						}
					})
					return false;
				}
				// 
				// if (res.data.code === -1) {
				// 	// , 
				// 	console.log('')
				// } else if (res.data.code === 0 || res.code === 0) {
					
				// }
				
				success && success(res.data);
	    },
			fail: function (res) {
				uni.showModal({
					title: '',
					content: ',',
					success: function (res) {
						if (res.confirm) {
								console.log('');
						} else if (res.cancel) {
								console.log('');
						}
					}
				})
				
			},
			complete: function (res) {
				uni.hideLoading();
			},
	});
}