// //  post 
	export const _post = function(url, data, success, fail, complete){
		
		let _this = this 
		// 
		// uni.showLoading({
		// 	title: '...',
		// 	mask:true
		// });
		
		// 
		// if(!wx.getStorageSync('token')){
		// 	uni.showModal({
		// 		title: '',
		// 		content: "",
		// 		success: function (res) {
		// 			if (res.confirm) {
		// 					console.log('  ');
		// 					uni.navigateTo({
		// 						url:"/pages/login/login"
		// 					})
							
							
		// 			} else if (res.cancel) {
		// 					console.log('');
							
		// 			}
		// 		}
		// 	})
		// 	return false
		// }
		
		
		
		
		
		
		// 
		data = data || {}
		// data.wxapp_id = App.siteInfo.uniacid;
		// data.token = wx.getStorageSync('token');
		//post
		console.log('post')

		uni.request({
			url:_this.api_root + url,  //
			header: {
				'Content-Type': 'application/json',
				'X-Xsh-Token': wx.getStorageSync('token') || ''
			},
			method: 'POST',
			data: data,
			success: res => {
				console.log('post ==res',res)
				if (res.statusCode !== 200 ) {
					uni.showModal({
						title: '',
						content: '',
						success: function (res) {
							if (res.confirm) {
									console.log('');
								
							} else if (res.cancel) {
									console.log('');
							}
						}
					})
					return false;
				}
				
				// 
				if(res.data.errno == 501){
					uni.showModal({
						title: '',
						content: res.data.errmsg,
						success: function (res) {
							if (res.confirm) {
									console.log('  ');
									uni.navigateTo({
										url:"/pages/login/login"
									})
									
									
							} else if (res.cancel) {
									console.log('');
							}
						}
					})
					return false
				}
				
				// 
				if(res.data.errno !== 0){
					uni.showModal({
						title: '',
						content: res.data.errmsg,
						success: function (res) {
							if (res.confirm) {
									console.log('');
							} else if (res.cancel) {
									console.log('');
							}
						}
					})
					return false;
				}
				        
				
				
				// 
				success && success(res.data)
			},
			fail: () => {
				uni.showModal({
					title: '',
					content: '',
					success: function (res) {
						if (res.confirm) {
								console.log('');
						} else if (res.cancel) {
								console.log('');
						}
					}
				})
			},
			complete: function (res) {
				uni.hideLoading()
				complete && complete(res);
			}
		});
	}
	
	
	
	